@font-face {
  font-family: cascadia;
  src: url('/src/components/fonts/Cascadia.ttf') format('truetype');
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: cascadia;
  scroll-behavior: smooth;
  cursor: default;
}

::-webkit-scrollbar{
  width: .5rem;
}

::-webkit-scrollbar-thumb{
  background: var(--cta-color);
}

:root{
  --cta-color: #6abaa3;
  --gradient-init: #0030AA;
  --gradient-end: rgb(80, 128, 114);
  --details-color: #C7FCEB;
  --white-color: #F1F1F1;
}

body{
  height: 100vh;
  background: linear-gradient(to top right, var(--gradient-init) 0%, var(  --gradient-end) 100%);
  background-attachment: fixed;
} 

#body-in-grid{
  width: 100%;
  height: 100%;
  position: absolute;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
}

.divisor{
  display: block;
  text-align: center;
  margin-top: 3rem;
  color: var(--details-color);
}

.externalUrl{
  color: var(--white-color);
  text-decoration: none;
}

#burguer-menu,  #mobile-filter, .name-container-mobile, .burger-container{
  display: none;
}

/* NAVBAR */

.navBar{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.presentation{
  border: 2px solid var(--cta-color);
  position: absolute;
  width: 100%;
  height: 98%;
  right: 0;
  top: 0;
  z-index: -1;
  transition: .2s;
}

.name-container{
  background-color: var(--cta-color);
  position: relative;
  width: max-content;
  height: max-content;
  padding: .8rem 1.5rem;
  margin-left: .5rem;
  margin-top: 1rem;
}

.name-container h1{
  font-size: 1.5rem;
  color: var(--white-color);
}

.name-container:hover .presentation, .work-box:hover .presentation, .contact-btns:hover .presentation{
  right: .5rem;
  top: .5rem;
}

.navigation{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 7rem;
  position: fixed;
}

.navigation li{
  list-style-type: none;
  font-size: 1.5rem;
  color: var(--cta-color);
  margin-top: 1rem;
  transition: .3s;
  padding-right: .5rem;
}

.navigation li:hover{
  color: var(--white-color);
  cursor: pointer;
  padding-right: 0;
  margin-left: .5rem;
}

/* ABOUT ME */

.photo-and-desc{
  display: flex;
  padding: 0 10%;
  align-items: center;
}

.my-photo{
  width: 10rem;
  height: 10rem;
  object-fit: cover;
  box-shadow: 0 0 15px var(--cta-color);
}

.description{
  margin-left: 2rem;
  text-align: justify;
}

/* KNOWLEDGE CONTAINER */

.knowledge:hover{
  color: var(--white-color);
}

.grid-box-knowledge{
  display: grid;
  grid-template-columns: 10% 90%;
  min-width: 50%;
  min-height: 10rem;
}

.knowledge-container{
  display: flex;
  flex-direction: row;
  padding: 0 5%;
  margin-top: 3rem;
  flex-wrap: wrap;
  justify-content: center;
}

.box-title{
  transform: rotate(270deg);
  height: min-content;
  position: relative;
  align-self: center;
  color: var(--cta-color);
  text-align: center;
}

.frontend-container, .backend-container, .db-container, .versionControl-container{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 90%;
  padding: 1rem 0;
  border: 2px solid transparent;
  border-color: var(--cta-color);
  margin: .5rem;
}

/* WORK CONTAINER */

.works-container{
  padding: 0 10%;
  margin-top: 3rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.work-box{
  width: 90%;
  height: 90%;
  position: relative;
  margin: 1rem;
}

.work:hover{
  border: 1px solid var(--gradient-cta-color);
}

.work-title{
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  filter: opacity(0);
  transition: .3s;
  color: var(--cta-color);
  cursor: pointer;
}

.work:hover .work-title{
  filter: opacity(1);
  top: -6%;
}

.work img{
  width: 100%;
  height: 100%;
  object-fit: scale-down;
  border: 2px solid var(--cta-color);
  box-shadow: 0 0 5px var(--cta-color);
}

/* CONTACT CONTAINER */

.contact-container{
  display: flex;
  justify-content: space-around;
  margin-top: 3rem;
  margin-bottom: 10%;
}

.contact-btns{
  display: flex;
  align-items: center;
  padding: .3rem 1rem;
  color: var(--cta-color);
  border: 2px solid var(--cta-color);
  box-shadow: 0 0 5px var(--cta-color);
  position: relative;
  background-color: #0f3cac;
  cursor: pointer;
  margin: 1rem;
  height: 100%;
  text-decoration: none;
}

/* SOCIAL MEDIA */

.social-media-container{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
  margin-right: 1rem;
  margin-bottom: 1rem;
  position: fixed;
  right: 0;
  bottom: 0;
}

.social-media{
  margin-top: 1rem;
  transition: .2s;
  border: 2px solid var(--cta-color);
}

.media-img{
  padding: .5rem;
  margin-bottom: -.2rem;
  cursor: pointer;
}

.social-media:hover{
  box-shadow: 0 0 10px var(--cta-color);
}


/*  */


.carrousel{
  width: 100%;
  max-width: 120em;
  overflow: hidden;
  margin: auto;
}

.grande{
  width: 95%;
  height: max-content;     
  display: flex; 
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  transition: all .5s ease;
  margin-top: 4rem;
}

.arrows{
  cursor: pointer;
  transition: .2s;
}

.arrows:hover{
  filter: drop-shadow(0 0 5px var(--white-color));
}