:root{
  --cta-color: #6abaa3;
  --gradient-init: #0030AA;
  --gradient-end: rgb(80, 128, 114);
  --details-color: #C7FCEB;
  --white-color: #F1F1F1;
}

@media only screen and (max-width: 500px){

    .notOverflow{
      overflow: hidden !important;
    }
  
    .photo-and-desc{
      margin-top: 3rem;
      padding: 0;
    }
  
    /* .social-media-container{
      display: none;
    } */

    .burger-container{
      display: block;
      position: absolute;
      position: fixed;
      right: 1rem;
      top: 1rem;
      border: 2px solid var(--cta-color);
      box-shadow: 0 0 5px var(--cta-color);
    }
  
    #burguer-menu{
      display: block;
      padding: .5rem;
    }
  
    #mobile-filter{
      background-image: linear-gradient(to top right, var(--cta-color), var(--gradient-init));
      width: 0;
      height: 100vh;
      z-index: 10;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 100vw;
      right: 0;
      margin: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      transition: .3s;
      overflow-x: hidden;
    }
  
    .moveFilter{
      width: 100vw !important;
      left: 0 !important;
    }
  
    #close-filter{
      position: absolute;
      right: 2rem;
      top: 2rem;
      color: var(--details-color);
      font-size: 1.5rem;
      font-weight: 600;
      transition: .3s;
      padding: .1rem .4rem;
    }

    .mobile-nav{
      height: 60%;
      display: flex;
      flex-direction: column;
      justify-content:space-evenly;
    }
  
    .filter-mobile-nav{
      list-style-type: none;
      color: var(--white-color);
      transition: .3s;
      margin: auto;
    }

    .filter-mobile-nav a{
      font-size: 1rem;
    }
  
    .presentation{
      margin-top: 1rem;
      margin-left: 5rem;
      width: 6rem;
      height: 2rem;
      position: absolute;
    }

    .name-container{
      display: none;
    }

    .name-container-mobile{
      display: block;
      background-color: var(--cta-color);
      position: relative;
      width: max-content;
      height: max-content;
      /* padding: .5rem 1rem; */
      padding: .5rem;
      /* margin-left: .5rem; */
      margin-top: 1rem;
      background-color: transparent;
      border: 2px solid var(--cta-color);
      box-shadow: 0 0 5px var(--cta-color);
    }

    .name-container-mobile img{
      width: 2rem;
      height: 2rem;
    }

    .name-container-mobile h1{
      font-size: 1.5rem;
    }
  
    .name-container p{
      font-size: .6rem;
    }
  
    .navigation{
      display: none;
    }
  
    .navigation li{
      font-size: .9rem;
      margin-left: 2rem;
    }
  
    /* ABOUT ME */
  
    .description{
      margin-left: 0;
    }
  
    /* KNOWLEDGE */
  
    .knowledge-container{
      padding: 0;
    }
  
    /* CONTACT */

    .social-media-container{
      margin-right: 1rem;
    }

    .social-media{
      padding: .2rem;
      margin-top: .5rem;
    }
  
    .media-img{
      padding: .1rem;
      height: 2rem;
      width: 2rem;
    }

    .contact-btns{
      width: max-content;
    }

    .contact-btns img {
      width: 2.5rem;
    }

    .presentation{
      display: none;
    }

    .arrows{
      width: 5%;
      height: 5%;
    }

    .grande{
      width: 96%;
    }

    .work:hover .work-title{
      filter: opacity(1);
      top: -12%;
    }
  }