@media only screen and (max-width: 768px){

    *{
        font-size: .8rem;
    }

    .name-container{
      margin-left: 2rem;
      padding: .6rem 1rem;
    }

    .description, .externalUrl, .navigation li, .name-container h1{
        font-size: 1rem;
    }

    .social-media-container{
        margin-right: 1rem;
    }

    .social-media{
        margin-top: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      
      .media-img{
        width: 80%;
        height: 80%;
        padding: .5rem .2rem;
        margin-bottom: -.1rem;
      }

      .contact-container{
        flex-wrap: wrap;
      }

}